import { Component, OnInit } from '@angular/core';
import * as model from '../../../shared/model';
import { SelectItem } from 'primeng/api';
import { ResizableColumn } from 'primeng/table';
import { HttpClient,HttpParams } from '@angular/common/http';
import { MultiSelectModule } from 'primeng/multiselect';
import { DialogModule } from 'primeng/dialog';
import { MessageService } from 'primeng/api';


import { BdToolBarComponent, OperateEnum } from '../../../shared/bd-tool-bar/bd-tool-bar.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';

export class FilterUnitSid{
  label:string;
  value:any
}

// Unit.Content.ts
@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.css']
})
export class ContentComponent implements OnInit {

  list2Grid:model.Unit[] = [];// 绑定在网格组件上的数组
  data2Editor:model.Unit;  // 绑定在编辑窗体上的模型实例
  selectedRow:model.Unit;  // 选中的行
  selectedCols:any[];// 显示的列

  urlGetAll:string = 'http://localhost:8085/femis/unitGetAll';
  urlAddNew:string = 'http://localhost:8085/femis/unitInsert';
  urlAddNew01:string = 'http://localhost:8085/femis/unitInsert01';
  urlDelete:string = 'http://localhost:8085/femis/unitDeleteByPrimaryKey';
  urlUpdate:string = 'http://localhost:8085/femis/unitUpdate';
  urlSelectValid:string = 'http://localhost:8085/femis/unit/getValid';
  urlSelectInvalid:string = 'http://localhost:8085/femis/unit/getInvalid';
  urlSetStatus:string = 'http://localhost:8085/femis/unit/setStatus/';


  cols:any[];

  statusList=[
    {label:'仅有效',value:'1'},
    {label:'仅无效',value:'0'},
    {label:'全部',value:'9'}
  ];
  status:string = '1';

  isAddNew:boolean;
  displayDialog:boolean;

  // 下面是字段为sid的列的过滤器
  filterSid:SelectItem[];
  staffs:SelectItem[] = [
    {label:'张三',value:'张三'},{label:'李四',value:'李四'},{label:'王五',value:'王五'}
  ];

  //下面都是删除时候弹出的抽屉组件的变量
  deleteDrawerPlacement:string = 'left';
  deleteDrawerVisible:boolean = false;//删除时弹出的抽屉组件的可见度

  constructor(
    private http:HttpClient,
    private msgService:MessageService,
  ) { }

  ngOnInit() {
    console.log('ngOnInit() Begin');

    this.initDeleteDrawer();//初始化删除时弹出的抽屉组件的状态

    console.log('unit.content.ngOnInt()');
    this.isAddNew = false;
    // this.getAll();
    this.selectValid();// 获取有效的数据
    this.cols = [
      {field:'sid',header:'计量单位'}
    ];

    this.selectedCols = this.cols;

    console.log('ngOnInit() End');
  }

  initFocus(){// 初始化第一个input获得焦点
    document.getElementById("sid").focus();
  }

  initDeleteDrawer(){
    this.deleteDrawerPlacement = 'left';
    this.deleteDrawerVisible = false;
  }

 
  showDialogToAdd() {// 显示新增的编辑窗体
    this.isAddNew = true;
    this.data2Editor = new model.Unit();
    this.displayDialog = true;

    this.initFocus();
  }

  save() {// 保存数据
    const llist2Grid = [...this.list2Grid];
    if (this.isAddNew) {
      llist2Grid.push(this.data2Editor);//向UI的集合数据中追加一个元素
      this.insert();//修改DB数据
    } else {
      llist2Grid[this.getSelectedRowIndex()] = this.data2Editor;//修改UI中的数据
      this.http.post(this.urlUpdate,this.data2Editor).subscribe(val=>{
        console.log('修改完毕了！');
      });
    }

    this.list2Grid = llist2Grid;
    this.data2Editor = null;
    this.displayDialog = false;
}

onRowSelect(event) {
  // this.isAddNew = false;
  this.data2Editor = {...event.data};
  this.data2Editor.oprimary = this.data2Editor.sid;
  // this.displayDialog = true;
}

showEditPage(data:model.Unit){
  if(data){
    this.isAddNew = false;
  }else{
    this.isAddNew = true;
  }
  this.data2Editor = data;
  this.displayDialog = true;
}

addNew(){
  this.data2Editor = new model.Unit();
  this.isAddNew = true;
  this.displayDialog = true;
}

insert(){// 新增
  this.http.post(this.urlAddNew,this.data2Editor).subscribe(
    (data:any)=>{
      console.log('即将被新增的对象是：');
      console.log(data);
      this.getAll();
    },
    error=>{
      console.log('新增时出错！');
    },
    ()=>{}
  );
}

insert01(){// 新增
  this.http.post(this.urlAddNew01,this.data2Editor,{
    params:new HttpParams().set('originSid','AAA')
  }).subscribe(
    (data:any)=>{
      console.log('即将被新增的对象是：');
      console.log(data);
      this.getAll();
    },
    error=>{
      console.log('新增时出错！');
    },
    ()=>{}
  );
}

//#region 删除
delete() {
  const index = this.getSelectedRowIndex();
  console.log('被选中的行的INDEX=' + index);
  var sid = this.list2Grid[index].sid;
  this.list2Grid = this.list2Grid.filter((val, i) => i !== index);// 删除UI数据
  this.http.get(this.urlDelete + '/' + sid).subscribe( // 删除DB中的数据
    (data)=>{
      console.log('即将被删除的主键是：' + sid);
    },
    error=>{
      console.log('删除数据时出错！');
    },
    ()=>{}
  );
  this.data2Editor = null;
  this.displayDialog = false;

  this.deleteDrawerVisible = false;
}
//#endregion


initFilterSid(data:any[]){// 初始化字段sid的过滤器
  this.filterSid = [];
  // data.forEach(element => {
  //   this.filterSid.push({label:element.sid,value:element.sid});
  // });
  this.filterSid = [
    {label:'个',value:'个'},{label:'米',value:'米'},{label:'匹',value:'匹'}
  ];
  
}



showGridData(url:string){
  this.http.get(url).subscribe(
    (data:any[])=>{
      // console.log('异步获取到的数据是：');
      // console.log(data);
      this.list2Grid = data;
      this.initFilterSid(data);//初始化字段sid的过滤器
      // console.log('list2Grid:');
      // console.log(this.list2Grid);
    },
    error=>{
      console.log('getAll()出错了！');
    },
    ()=>{}
  );
}

selectValid(){
  this.showGridData(this.urlSelectValid);
}

selectInvalid(){
  this.showGridData(this.urlSelectInvalid);
}

getAll(){
  this.showGridData(this.urlGetAll);
}

  getSelectedRowIndex(): number {
    return this.list2Grid.indexOf(this.selectedRow);
  }

  onMenuItemClick(event:OperateEnum){
    if(event === OperateEnum.insert) {// 新增
      this.addNew();
    }else if (event === OperateEnum.update) {// 修改
      this.update();
    }else if( event === OperateEnum.delete ) {// 删除
      this.deleteDrawerVisible = true;
    }else if(event === OperateEnum.obsolete){
      this.setInvalid();
    }else if(event === OperateEnum.cancelObsolete){
      this.setValid();
    }else if(event === OperateEnum.export){
      document.getElementById("btnExport").click();
    }
    else {
      console.log('是其他操作');
    }

  }

  onDblClick(rowIndex:number){// 双击弹出编辑页面
    let data:model.Unit = this.list2Grid[rowIndex];
    this.showEditPage(data);
  }

  update(){
    console.log(this.data2Editor);
    if(this.data2Editor){
      this.isAddNew = false;
    }else{
      return;
    }
    this.displayDialog = true;
    this.initFocus();
  }


  closeDeleteDrawer(){
    this.deleteDrawerVisible = false;
  }


  afterColResize(event){
    console.log('列宽度变动结束了');
    console.log(event.element);
    console.log(event.delta);
    console.log(event.element.offsetWidth);
  }

  afterSort(event){
    console.log('重新排序的字段是：' + event.field);
  }

  onStatusChange(){
    console.log(this.status);

    if(this.status=='1'){
      console.log('仅显示有效数据');
      this.selectValid();
    }else if(this.status=='0') {
      console.log('仅显示无效数据');
      this.selectInvalid();
    }else{
      console.log('显示所有数据');
      this.getAll();
    }
  }

  setStatus(status:number){
    if(!this.data2Editor){
      this.msgService.add(
        {severity:'info',summary:'操作失败：',detail:'鼠标左键选中一行后再执行本操作！'}
      );
      console.log('没有选中任何行！');
      return;
    }

    var url = this.urlSetStatus + '/' + this.data2Editor.sid + '/' + status;
    this.http.get(url).subscribe(val=>{
      console.log('修改了sid = ' + this.data2Editor.sid + '的状态为：' + status);
    });
  }

  setValid(){
    this.setStatus(1);
  }

  setInvalid(){
    this.setStatus(0);
  }

}
